import React, {useEffect} from 'react';
import {Cart, Foods, Header, HeaderCategory, MobileCart, Preloader} from "./components";
import {connect} from "react-redux";
import {appActions} from "./redux/actions";

function App({cartVisible, init, preloader}) {
    useEffect(() => {
        init();
    }, [])

    if(preloader) {
        return  <Preloader />
    }

    return (
        <div className="App">
            <Header/>
            <div style={{margin: "0 auto", maxWidth: "1280px"}}>
                <HeaderCategory/>
                <Foods/>

                {/*{(/Android|webOS|iPhone|iPad|iPod|BlackBerry/i.test(navigator.userAgent)) ? <MobileCart/> : ''}*/}

                {cartVisible && <Cart/>}
            </div>
        </div>
    );
}

const mapStateToProps = (state) => {

    return {
        cartVisible: state.cartReducer.visible,
        preloader: state.preloaderReducer
    }
}

export default connect(mapStateToProps, {...appActions})(App);
