import React from 'react';
import './index.css';
import MobileCatalog from "./components/MobileCatalog";
import {connect} from "react-redux";



const HeaderCategory = ({items}) => {
    return (
        <div className={"MobileCatalogPageFilters_container d-flex justify-content-center"}>
            <MobileCatalog items={items}/>
        </div>

    )
};

const mapStateToProps = state => {
    return {
        items: state.categoryReducer.items
    }
}

export default connect(mapStateToProps)(HeaderCategory);