import React from 'react';

export default ({name, price, count, img, increment, decrement, remove, options, sum}) => {
    return (
        <li className="cart__item">
            <div className="cart-product__remove" onClick={remove}>
                <svg version="1.1" viewBox="0 0 212.982 212.982" className="icon svg-icon svg-fill"
                     style={{width: "1em", height: "1em"}}>
                    <path pid="0"
                          d="M131.804 106.491l75.936-75.936c6.99-6.99 6.99-18.323 0-25.312-6.99-6.99-18.322-6.99-25.312 0L106.491 81.18 30.554 5.242c-6.99-6.99-18.322-6.99-25.312 0-6.989 6.99-6.989 18.323 0 25.312l75.937 75.936-75.937 75.937c-6.989 6.99-6.989 18.323 0 25.312 6.99 6.99 18.322 6.99 25.312 0l75.937-75.937 75.937 75.937c6.989 6.99 18.322 6.99 25.312 0 6.99-6.99 6.99-18.322 0-25.312l-75.936-75.936z"
                          fillRule="evenodd" clipRule="evenodd"></path>
                </svg>
            </div>
            <div className="cart-product__count">
                <div className="product__count-btn decrement" onClick={decrement}>
                    <svg version="1.1" viewBox="0 0 459.313 459.314"
                         className="icon decrement-icon svg-icon svg-fill"
                         style={{width: "1em", height: "1em"}}>
                        <path pid="0"
                              d="M459.313 229.648c0 22.201-17.992 40.199-40.205 40.199H40.181c-11.094 0-21.14-4.498-28.416-11.774C4.495 250.808 0 240.76 0 229.66c-.006-22.204 17.992-40.199 40.202-40.193h378.936c22.195.005 40.17 17.989 40.175 40.181z"></path>
                    </svg>
                </div>
                <div className="count">{count}</div>
                <div className="product__count-btn increment" onClick={increment}>
                    <svg version="1.1" viewBox="0 0 448 448"
                         className="icon increment-icon svg-icon svg-fill"
                         style={{width: "1em", height: "1em"}}>
                        <path pid="0"
                              d="M408 184H272a8 8 0 0 1-8-8V40c0-22.09-17.91-40-40-40s-40 17.91-40 40v136a8 8 0 0 1-8 8H40c-22.09 0-40 17.91-40 40s17.91 40 40 40h136a8 8 0 0 1 8 8v136c0 22.09 17.91 40 40 40s40-17.91 40-40V272a8 8 0 0 1 8-8h136c22.09 0 40-17.91 40-40s-17.91-40-40-40zm0 0"></path>
                    </svg>
                </div>
            </div>
            <div className="cart-product__wrapper">
                <div className="cart-product__img">
                    <img
                        src={img} alt=""/></div>
                <div className="cart-product__body">
                    <div className="cart-product__title">
                        {name}

                        {options && options.map(item => {
                            return (
                                <div className={'cart-product__item'} key={item.id}>{item.name}<small>x</small>{item.quantity}</div>
                            )
                        })}
                    </div>
                    <div className="cart-product__title"></div>
                    <div className="cart-product__price">{sum}₽</div>
                </div>
            </div>
        </li>
    )
};