import React from 'react';

export default ({name, icon, slug}) => (


    <li className={"MobileCatalogPageFilters_filter"}>
        <a href={`#${slug}`}>
            <i className={"MobileCatalogPageFilters_filterIcon lazyload"} style={{backgroundImage: `url("${icon}")`}}
               data-src={icon}></i>
        </a>
        <a href={`#${slug}`} className={"MobileCatalogPageFilters_filterName"} data-href={`#${slug}`}>{name}</a>
    </li>
)