import React from 'react';
import CategoryMenuItem from "./itemCatalog";

const MobileCatalog = ({items}) => {
    return (
        <ul className={"MobileCatalogPageFilters_filters"}>
            {items && items.map((item, key) => {
                return <CategoryMenuItem name={item.name} icon={item.icon} slug={item.slug} key={key}/>
            })}

        </ul>
    )
}

export default MobileCatalog