import React, {useEffect} from 'react';
import './index.css';
import {connect} from "react-redux";
import {cartActions} from "../../redux/actions";

const CartButton = ({setCartVisible, total}) => {
    useEffect(() => {}, [])
    return (
        <li>
            <a href="/#" className="header__cart" onClick={() => setCartVisible(true)}>
                <span className="header__cart-txt"><i className="material-icons">shopping_cart</i></span> <span
                className="header__cart-counter">{total}</span></a>
        </li>
    )
}

const mapStateToProps = state => {
    return {
        count: state.cartReducer.items?.length,
        total: state.cartReducer.total,

    }
}

export default connect(mapStateToProps, {...cartActions})(CartButton);