import React, {useEffect, useState} from 'react'
import './index.css'
import {connect} from "react-redux";
import {appActions, cartActions} from "../../redux/actions";
import CartItem from "./_cartItem";
import {CartApi} from "../../core/api";

const Cart = ({setCartVisible, total, cartItems, setCartItem, decrementCartItem, setCartItems, setCartTotal, getCart}) => {
    const closeCart = () => {
        setCartVisible(false);
    }

    const [isCheckout, setIsCheckOut] = useState(false);
    const [client, setClient] = useState(null);
    const [isLoading, setLoading] = useState(false);
    const [errors, setErrors] = useState(null)

    useEffect(() => {

    }, [])

    const handleChange = e => {
        setClient({
            ...client,
            [e.target.name]: e.target.value
        })
    }

    const checkError = field => {
        return errors && errors[field] && errors[field][0]
    }

    const send = () => {
        setLoading(true);
        setErrors(null);
        CartApi.save(client)
            .then(response => {
                localStorage.setItem('token', Math.random());
                setErrors(null);
                setCartItems([])
                setCartTotal(0)
                closeCart();
                setIsCheckOut(false);
                window.M.toast({html: 'Ваш заказ успешно добавлен. Ожидайте звонка:)'})
            })
            .catch(error => {
                setErrors(error.response.data.errors)
            })
            .finally(() => {
                setLoading(false);
                // setIsCheckOut(false);
            })
    }

    const destroyProperty = id => {
        CartApi.destroyProperty(id)
            .then(response => {
                getCart()
            })
            .catch(error => {

            })
    }

    return (
        <div className={"cart"}>
            <div className="cart__wrapper">
                <div className="cart__header">
                    <span className={"cart__title"}>Корзина</span>
                    <div className="cart__icons">
                        {isCheckout && <span className="cart__icon" onClick={setIsCheckOut.bind(null, false)}
                                             style={{marginRight: 10}}>
                            <svg version="1.1" viewBox="0 0 31.494 31.494"
                                 className="left-arrow-icon svg-icon svg-fill"
                                 style={{
                                     width: "1em",
                                     height: "1em"
                                 }}><path pid="0"
                                          d="M10.273 5.009a1.112 1.112 0 0 1 1.587 0 1.12 1.12 0 0 1 0 1.571l-8.047 8.047h26.554c.619 0 1.127.492 1.127 1.111s-.508 1.127-1.127 1.127H3.813l8.047 8.032c.429.444.429 1.159 0 1.587a1.112 1.112 0 0 1-1.587 0L.321 16.532a1.12 1.12 0 0 1 0-1.571l9.952-9.952z"
                                          _fill="#1e201d"></path>
                            </svg>
                        </span>}

                        <span onClick={closeCart} className="cart__icon">
                            <svg version="1.1"
                                 viewBox="0 0 512 512"
                                 className="close-icon svg-icon svg-fill"
                                 style={{
                                     width: "1em",
                                     height: "1em"
                                 }}><path
                                pid="0"
                                d="M505.943 6.058c-8.077-8.077-21.172-8.077-29.249 0L6.058 476.693c-8.077 8.077-8.077 21.172 0 29.249A20.612 20.612 0 0 0 20.683 512a20.614 20.614 0 0 0 14.625-6.059L505.943 35.306c8.076-8.076 8.076-21.171 0-29.248z"></path><path
                                pid="1"
                                d="M505.942 476.694L35.306 6.059c-8.076-8.077-21.172-8.077-29.248 0-8.077 8.076-8.077 21.171 0 29.248l470.636 470.636a20.616 20.616 0 0 0 14.625 6.058 20.615 20.615 0 0 0 14.624-6.057c8.075-8.078 8.075-21.173-.001-29.25z"></path></svg></span>
                    </div>
                </div>

                <div className="cart__body">
                    {
                        0 === cartItems.length &&
                        <div className={"d-flex align-items-center justify-content-center "}
                             style={{fontWeight: "bold"}}>Ваша корзина пуста :(</div>}
                    {
                        (false === isCheckout) ?
                            <ul className="cart__list">
                                {cartItems && cartItems.map((cartItem, key) => {
                                    return (
                                        <CartItem
                                            key={key}
                                            count={cartItem.quantity}
                                            img={cartItem.img}
                                            name={cartItem.name}
                                            price={cartItem.price}
                                            sum={cartItem.sum}
                                            increment={setCartItem.bind(this, cartItem.foodPropertyId, {})}
                                            decrement={decrementCartItem.bind(this, cartItem.id, cartItem.quantity - 1)}
                                            remove={destroyProperty.bind(null, cartItem.id)}
                                            options={cartItem.options}
                                        />
                                    )
                                })}

                            </ul>
                            :

                            <div className="row">
                                <form className="col s12">
                                    <div className="row">
                                        <div className="input-field col s12">
                                            <i className="material-icons prefix">account_circle</i>
                                            <input onChange={handleChange} id="name" name={"name"} type="text"
                                                   className={`validate ${checkError('name') ? 'invalid' : ''} `}/>
                                            <label htmlFor="name">Ваше имя</label>
                                            <span className="helper-text" data-error={checkError('name')}
                                                  data-success=""></span>
                                        </div>
                                        <div className="input-field col s12">
                                            <i className="material-icons prefix">phone</i>
                                            <input onChange={handleChange} id="phone" name={"phone"} type="text"
                                                   className={`validate ${checkError('name') ? 'invalid' : ''} `}/>
                                            <label htmlFor="phone">Ваш телефон</label>
                                            <span className="helper-text" data-error={checkError('phone')}
                                                  data-success=""></span>
                                        </div>
                                    </div>

                                    <div className="row">
                                        <div className="input-field col s12">
                                            <input onChange={handleChange} id="city" name={"city"} type="text"
                                                   className={`validate ${checkError('city') ? 'invalid' : ''} `}/>
                                            <label htmlFor="city">Город</label>
                                            <span className="helper-text" data-error={checkError('city')}
                                                  data-success=""></span>

                                        </div>

                                        <div className="input-field col s12">
                                            <input onChange={handleChange} id="street" name={"street"} type="text"
                                                   className={`validate ${checkError('street') ? 'invalid' : ''} `}/>
                                            <label htmlFor="street">Улица</label>
                                            <span className="helper-text" data-error={checkError('street')}
                                                  data-success=""></span>

                                        </div>

                                        <div className="input-field col s12">
                                            <input onChange={handleChange} id="house" name={"house"} type="text"
                                                   className={`validate ${checkError('house') ? 'invalid' : ''} `}/>
                                            <label htmlFor="house">Дом/Квартира</label>
                                            <span className="helper-text" data-error={checkError('house')}
                                                  data-success=""></span>

                                        </div>
                                    </div>

                                    <div className={"row"}>
                                        <div className={"col s12"}>
                                            <div className="input-field col s12">
                                            <textarea onChange={handleChange}
                                                      className={`validate ${checkError('comment') ? 'invalid' : ''} `}
                                                      name={"comment"} id="comment"
                                                      className="materialize-textarea"></textarea>
                                                <label htmlFor="comment">Комментарий</label>
                                                <span className="helper-text" data-error={checkError('comment')}
                                                      data-success=""></span>

                                            </div>
                                        </div>
                                    </div>

                                </form>
                            </div>
                    }


                </div>
                {cartItems && cartItems.length > 0 && <div className="cart__footer">
                    <div className="cart__total">
                        <div className="cart__row"><span className="cart__total-light-text">Итого</span> <span
                            className="cart__total-amount"><span className="price"> {total}</span> <span
                            className="rouble-icon">₽</span></span>
                        </div>
                    </div>
                    <div>
                        <button disabled={isLoading}
                                onClick={(false === isCheckout) ? setIsCheckOut.bind(null, true) : send} type="button"
                                className="button cart__button">
                            <div className={"d-flex align-items-center"}>
                                {isLoading && <i className="fas fa-spinner fa-spin"></i>}
                                <span style={{marginLeft: 5}}>Оформить</span>
                            </div>
                        </button>
                    </div>


                </div>}

            </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        cartItems: state.cartReducer.items,
        total: state.cartReducer.total,
    }
}

export default connect(mapStateToProps, {...appActions, ...cartActions})(Cart);